<template>
  <div>
    <topMember></topMember>
    <div class="box mar fffbg" style="min-height:500px;">
      <div class="mainH">
        <div class="mainHICO"></div>
        <span class="pull-left">资金明细</span>
      </div>
      <div class="pad">
        账户余额：<strong class="red f20">￥{{mytotal/100}}</strong><br/>
        <div class="mar">
          <el-select v-model="type" placeholder="请选择">
             <el-option  key="1" label="充值" value="1"></el-option>
              <el-option  key="2" label="消费" value="2"></el-option>
              <el-option  key="3" label="退费" value="3"></el-option>
              <el-option  key="4" label="提现" value="4"></el-option>
          </el-select>
        <el-date-picker
            v-model="dateValue"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
        <el-button @click="search" type="primary">查询</el-button>
        </div>
        <div class="mar">
          <el-table
            :data="tableData"
            border
            :header-cell-style="{background:'#f7f7f7'}"
            style="width: 100%"
          >
            <el-table-column prop="createTime" label="日期" align="center" width="200"></el-table-column>
            <el-table-column prop="type" align="center" label="款项">
                <template slot-scope="scope">
                    <span v-if="scope.row.type==1">充值</span>
                    <span v-if="scope.row.type==2">消费</span>
                    <span v-if="scope.row.type==3">退费</span>
                    <span v-if="scope.row.type==4">提现</span>
                </template>
            </el-table-column>
            <el-table-column  align="center" label="金额">
                <template slot-scope="scope">
                    {{scope.row.money/100}} 元
                </template>
            </el-table-column>
            <el-table-column align="center" label="状态">
                <template slot-scope="scope">
                     <span class="red" v-if="scope.row.deleteFlag==false">成功</span>
                     <span v-if="scope.row.deleteFlag==true">失败</span>
                </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pageDiv">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <foot></foot>

   
  </div>
</template>

<script>
import foot from "./public/foot.vue";
import topMember from "./public/topMember.vue";
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
      type:'',
      page:1,
      size:10,
      tableData: [],
      total:0,
      dateValue:'',
      mytotal:0
    };
  },
  mounted(){
    this.search();
    this.getMoney();
  },
  methods: {
    getMoney(){
      let that=this;
        this.axios
          .get(
            // process.env.API_HOST+"wallet/myTotal",
            "/giftmall/wallet/myTotal",
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                "domain": this.url,
                "Authorization":sessionStorage.getItem('token')
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
                that.mytotal=response.data.data
            } 
          });
    },
    search(){
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"wallet?begin="+that.dateValue[0]+'&end='+that.dateValue[1]+'&type='+that.type+'&page='+(that.page-1)+'&size='+that.size,
          "/giftmall/wallet?begin="+(that.dateValue[0] || '') +'&end='+(that.dateValue[1] || '') +'&type='+that.type+'&page='+(that.page-1)+'&size='+that.size,
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             for(var i=0;i<response.data.data.list.length;i++){
                response.data.data.list[i].createTime=that.gettime(response.data.data.list[i].createTime);
             }
             that.tableData=response.data.data.list;
             that.total=response.data.data.totalCount;
          } 
        });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size=val;
      this.search();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.page=val;
      this.search();
    },
    gettime(t){
        var _time=new Date(t);
        var   year=_time.getFullYear();//2017
        var   month=_time.getMonth()>8?(_time.getMonth()+1):'0'+(_time.getMonth()+1);//7
        var   date=_time.getDate()>9?_time.getDate():'0'+_time.getDate();//10
        var   hour=_time.getHours()>9?_time.getHours():'0'+_time.getHours();;//10
        var   minute=_time.getMinutes()>9?_time.getMinutes():'0'+_time.getMinutes();;//56
        var   second=_time.getSeconds()>9?_time.getSeconds():'0'+_time.getSeconds();;//15
        
        return   year+"-"+month+"-"+date+"   "+hour+":"+minute+":"+second;//这里自己按自己需要的格式拼接
    }
  }
};
</script>

<style>

</style>




